// components/FinalDesign.js
import React from 'react';
import Finaldesignrender from '../assets/img/Finaldesign render.png'
import Finaldesignrender2 from '../assets/img/Finaldesign render2.png'
import Finaldesignrender3 from '../assets/img/Finaldesign render3.png'
import Finaldesignrender4 from '../assets/img/Finaldesign render4.png'
import Finaldesignrender5 from '../assets/img/Finaldesign render5.png'
import us1 from '../assets/img/us1.mp4'

const FinalDesign = () => {
  return (
    <section id="final">
      <div className="final-container">
        <h2 className='final-h2'>FINAL DESIGN</h2>
        <p className="final-paragraph">
          After the user research was completed, I created the first version of a high-fidelity prototype based on the revised wireframe and user feedback. After testing, the first version of the high-fidelity prototype showed a good user experience. Corresponding to the wireframe, I demonstrated the high-fidelity prototype through three user scenarios.
        </p>
        <h2 className="final-header">UI Design (plan your itinerary on desktop)</h2>
        <div className="final-video1">
          <video className="video-player" controls>
            <source src={us1} type="video/mp4" />
          </video>
        </div>
        <div className="final-image-area">
          <img src={Finaldesignrender}></img>
        </div>
        <div className="final-image-area">
          <img src={Finaldesignrender2}></img>
        </div>
        <div className="final-image-area">
          <img src={Finaldesignrender3}></img>
        </div>
        <div className="final-image-area">
          <img src={Finaldesignrender4}></img>
        </div>
        <div className="final-image-area">
          <img src={Finaldesignrender5}></img>
        </div>
      </div>
    </section>
  );
};

export default FinalDesign;