import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import portrait from '../assets/img/portrait2.jpg';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  const toRotate = [ "UI/UX Designer", "Code Newbie", "Car Enthusiast" ];
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center" style={{ display: 'flex' }}>
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <h1>{`Hi there👋🏼, I'm Qian Jinfan A`} <span className="txt-rotate" dataPeriod="1000" data-rotate='[ "UI/UX Designer", "Code Newbie", "Car Enthusiast" ]'><span className="wrap">{text}</span></span></h1>
                  <p>Welcome to Qian Jinfan's personal homepage. I am currently enrolled in the Art and Technology program at Zhejiang University. I have a strong passion for designing products that are user-friendly, convenient, and highly efficient. In my free time, I enjoy watching F1 races, going for drives, and playing badminton. And most importantly, I can't wait to dive into Genshin Impact!</p>
                  <button onClick={() => console.log('connect') }>SCROLL to EXPLORE <ArrowRightCircle size={25} /></button>
              </div>}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={8} xl={5} style={{ display: 'flex', justifyContent: 'center' }}>
            <figure>
              <img src={portrait} />
            </figure>
          </Col>
        </Row>
      </Container>
    </section>
  )
}