import render1 from "../assets/img/render1.png";
import Designprogress from "../assets/img/Designprogress.png";
import './Project1.css'

const Background = () => {
  return (
    <section id="background">
      {/* 顶部图片 */}
      <header className="Project1-header">
        <img src={render1} alt="" />
      </header>

      {/* 正文内容 */}
      <div className="Project1-header2">
        {/* 左侧区块：项目标题与各项信息 */}
        <aside className="Project1-sidebar">
          <h1>PlanWings</h1>
          <p>August 2023 - Now</p>
          <p>Work-In-Progress</p>
          <button>Interaction Design</button>
          <button>Information Architecture</button>
          <button>Personal Project</button>
        </aside>

        {/* 右侧区块：项目背景介绍 */}
        <section className="Project1-background">
          <h2>BACKGROUND</h2>
          <p style={{ fontSize: '14px', color: '#707070' }}>In August 2023, I visited Japan for tourism. After a week of sightseeing, our trip ended with a return flight from KIX. Despite checking Google Maps and Xiaohongshu for traffic updates, we almost got lost trying to find the right train platform to the airport. When we finally reached the airport, the check-in counter was closed. Thankfully, the airport staff helped us check-in quickly and guided us through a fast-track security lane, so we just made our flight.</p>
          <p style={{ fontSize: '14px', color: '#707070' }}>Later, I found out that many of my friends had similar experiences when flying from foreign airports. They often feel anxious and worried, facing unexpected situations that can cause them to miss their flights.</p>
          <p style={{ fontSize: '16px', color: '#454545', fontStyle: 'italic' }}>So, how might we help people who travel abroad to catch flights easily and confidently?</p>
          {/* 添加更多内容... */}
        </section>
      </div>

      <img src={Designprogress} className="Designprogress"></img>
    </section>
  );
}

export default Background;