import { Col, Container} from "react-bootstrap";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import { NavLink } from "react-router-dom";

export const ProjectCard = ({ title, title2, time, description, description2, imgUrl }) => {
  return (
    <Container className="d-flex">
        <Col size={20} sm={6} md={10}>
          <div className="proj-imgbx">
            <img src={imgUrl} />
            <div className="proj-txtx">
              <h4>{title}</h4>
              <span>{description}</span>
            </div>
          </div>
        </Col>
        <Col>
          <div className="proj-text">
            <h3 className="proj-text-h3">{title2}</h3>
            <p style={{color:'#AAAAAA', margin:'10px 0px 10px 0px', textAlign:"left", fontSize:'small'}}>{time}</p>
            <p style={{margin:'20px 0px 150px 0px', textAlign:"left", color:'#AAAAAA', minWidth:'500px', fontStyle: 'italic'}}>{description2}</p>
            <button onClick={() => console.log('connect') } className="clicktoexplore">
              <NavLink to="/project1" >
                CLICK to EXPLORE <ArrowRightCircle size={25} />
              </NavLink>
            </button>
          </div>
        </Col>
    </Container>
  )
}